import { isDevelopment } from "../../app/store";

function getDB() {
  return new Promise(function (resolve, reject) {
    if (!window.indexedDB) {
      reject("Indexed DB does not exists");
    }

    var indexedDbRequest = window.indexedDB.open("testSave", 1);

    indexedDbRequest.onupgradeneeded = function () {
      var db = this.result;
      db.createObjectStore("savestring");
    };

    indexedDbRequest.onerror = function (ev) {
      reject("Failed to get IDB ".concat(ev));
    };

    indexedDbRequest.onsuccess = function () {
      var db = this.result;

      if (!db) {
        reject("database loading result was undefined");
        return;
      }

      resolve(db.transaction(["savestring"], "readwrite").objectStore("savestring"));
    };
  });
}

export function load() {
  return new Promise(function (resolve, reject) {
    getDB().then(function (db) {
      return new Promise(function (resolve, reject) {
        var request = db.get(getDbName());

        request.onerror = function (ev) {
          reject("Error in Database request to get savestring: " + ev);
        };

        request.onsuccess = function () {
          resolve(this.result);
        };
      }).then(function (saveString) {
        return resolve(saveString);
      });
    })["catch"](function (r) {
      return reject(r);
    });
  });
}

function getDbName() {
  return isDevelopment ? "dev" : "save";
}

export function save(saveString) {
  return getDB().then(function (db) {
    return new Promise(function (resolve, reject) {
      var request = db.put(saveString, getDbName());

      request.onerror = function (e) {
        reject("Error saving game to IndexedDB: " + e);
      };

      request.onsuccess = function () {
        return resolve();
      };
    });
  });
}
export function deleteGame() {
  return getDB().then(function (db) {
    db["delete"](getDbName());
  });
}